import isDebug from '@/utils/isDebug';
import { useState, useCallback } from 'react';
import useEventListener from './useEventListener';

/**
 * 如果在调试模式下, 监听 post message,
 */
export default () => {
  const [count, setCount] = useState(0);

  const countPlusPlus = useCallback(
    (event: MessageEvent) => {
      if (event.data && event.data.action === 'tt-update') {
        setCount(c => c + 1);
      }
    },
    [setCount]
  );

  useEventListener('message', countPlusPlus, window, window === window.self && isDebug());

  return count;
};
