/**
 * 提供给非 react 的老项目使用
 */
import Container from '@/components/Container';
import React from 'react';
import ReactDOM from 'react-dom';
import { StateContainer } from 'state-container';
import Themeable, { ThemeLoaderProps } from './Themeable';
import { ColorSchemeSettingI } from './typings/ColorScheme';

const RENDER_NODE_ID = 'themeable-render-node';

function getRenderNode() {
  let node = window.document.querySelector(`#${RENDER_NODE_ID}`);
  if (node) return node;
  node = document.createElement('div');
  node.setAttribute('id', RENDER_NODE_ID);
  window.document.querySelector('body')?.appendChild(node);
  return node;
}

function render(stateContainer: StateContainer, props: ThemeLoaderProps) {
  const node = getRenderNode();

  ReactDOM.unmountComponentAtNode(node);
  ReactDOM.render(
    <Container stateContainer={stateContainer}>
      <Themeable {...props} />
    </Container>,
    node
  );
}

interface Args extends ThemeLoaderProps {
  stateContainer: StateContainer;
}

export default async ({ stateContainer, ...restProps }: Args) => {
  render(stateContainer, restProps);

  // 切换颜色模式
  function switchColorScheme(css: ColorSchemeSettingI) {
    render(stateContainer, { ...restProps, colorScheme: css });
  }

  return switchColorScheme;
};
